<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process138"></a>

<p><br></p>
<p><span class="rvts17">永恒的崇拜者（</span><span class="rvts50">The Eternal Worshipper</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X703:,Y:501</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：格兰缇娜（</span><span class="rvts20">Gratiana</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">把格兰缇娜的灵柩罐交给她</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1076.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在</span><span class="rvts20"></span><span class="rvts21">布拉克斯</span><span class="rvts20"></span><span class="rvts21">雷克斯藏宝室</span><span class="rvts20"></span><span class="rvts21">支线任务里，可以在藏宝点发现</span><span class="rvts20">4</span><span class="rvts21">个灵柩罐，其中一个是格兰缇娜的。把他的灵柩罐带到阿玛蒂亚避难所，交给她。该任务完成。</span>
<br><span class="rvts21">格兰缇娜的位置在祈祷雕像（传送点）附近，是个大头不死族牧师。如果的劝告属性值较高，可以让格兰缇娜为你的</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">魔杖充能，用于破除更多的嚎叫者。</span>
<br><span class="rvts21">格兰缇娜曾和布拉克斯是同一战线的爱人和伙伴，然而，各项迹象表明，它是雷克斯死亡的真正幕后凶手。但是，如果拯救了布兰缇娜，它会加入你对抗亚历山大的队伍。</span>
<br><span class="rvts21">格兰缇娜在所有角色登上复仇女士号后，最终消失在所有人的视野中，不再露面。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



